import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getListPrizeInTrainType, deletePrizeInTrainType } from 'services/PrizeInTrainType';

class StipulateAmountOfScholarshipPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deletePrizeInTrainType(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;

    const columns = [
      {
        title: 'M xp loi',
        dataIndex: 'prizeClassId',
        key: 'prizeClassId',
      },
      {
        title: 'Xp loi',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'H',
        dataIndex: 'trainTypeLabel',
        key: 'trainTypeLabel',
      },
      {
        title: 'T im HT 10',
        dataIndex: 'gradeIn10',
        key: 'gradeIn10',
      },
      {
        title: 'T im HT 4',
        dataIndex: 'gradeIn4',
        key: 'gradeIn4',
      },
      {
        title: 'T im RL',
        dataIndex: 'gradeInPractice',
        key: 'gradeInPractice',
      },
      {
        title: 'S tin',
        dataIndex: 'amount',
        key: 'amount',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#scholarship/edit/${record.prizeClassId}/${record.trainTypeId}`}
            onConfirmDelete={() => this.handleDelete(record.prizeClassId)}
          />
        ),
      },
    ];
    return (
      <Card title="Quy nh xp loi hc bng">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/scholarship/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getListPrizeInTrainType}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StipulateAmountOfScholarshipPage />
  </Page>
);



