import { request } from 'utils';

request.prefix = '/api/categories';

export const getListPrizeInTrainType = ({ page, pageSize = 10, isPagingEnabled = true }) =>
  request({
    url: '/PrizeInTrainType',
    params: {
      page,
      pageSize,
      isPagingEnabled,
    },
    method: 'GET',
  });
export const getPrizeInTrainType = id =>
  request({
    url: `/PrizeInTrainType/GetById/${id}`,
    method: 'GET',
  });
export const deletePrizeInTrainType = id =>
  request({
    url: `/PrizeInTrainType/${id}`,
    method: 'DELETE',
  });
export const updatePrizeInTrainType = (id, values) =>
  request({
    url: `/PrizeInTrainType/${id}`,
    data: {
      id: values.id,
      label: values.code,
      sfId: null,
      labelInEnglish: values.labelInEnglish || '',
      trainTypeId: values.trainTypeId || '',
    },
    method: 'PUT',
  });

export const createPrizeInTrainType = values =>
  request({
    url: '/PrizeInTrainType',
    data: {
      trainTypeId: values.trainTypeId,
      prizeClassId: values.prizeClassId,
      amount: values.amount,
    },
    method: 'POST',
  });

export const listPrizeClassification = () =>
  request({
    url: '/PrizeClassification',
    params: {
      isPagingEnabled: false,
    },
    method: 'GET',
  });



